@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 414px){.desktop{display:none}}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*,*::before,*::after{box-sizing:border-box}.d-flex{display:flex}.flex-column{flex-direction:column}.justify-content-center{justify-content:center}.align-item-center{align-items:center}.sidebar{width:25%;height:100vh;position:fixed;z-index:998;background-color:#339ad3;box-shadow:0.25rem 0.25rem 0.75rem rgba(72,164,216,0.8);transition:opacity .3s, transform .3s;opacity:1}.sidebar.hide{opacity:0;transform:translateX(-100%);z-index:0}@media screen and (max-width: 414px){.sidebar.hide{transform:translateY(-100%)}}@media screen and (max-width: 992px){.sidebar{z-index:9999;width:35%}}@media screen and (max-width: 414px){.sidebar{width:100%}}.sidebar__container{height:100%;overflow:hidden}.searchBtn{position:absolute;z-index:999;background-color:#fff;border:1px solid #5dafdc;border-radius:0 10px 10px 0;font-size:18px;padding:.25rem .75rem .25rem .25rem;transition:.3s;left:0;outline:none !important;cursor:pointer}@media screen and (max-width: 992px){.searchBtn{z-index:100000;font-size:28px}}.searchBtn:hover{background-color:#87c4e5;opacity:1}.searchBtn.show{left:25%}@media screen and (max-width: 992px){.searchBtn.show{left:35%}}@media screen and (max-width: 414px){.searchBtn.show{left:0;opacity:.8}}.select__container{width:100%;padding:0.5rem 0;border-bottom:2px solid #257dae}.select__item{width:88%;border-width:1px;border-style:solid;border-color:#888;border-radius:10px;color:#888;padding:.75rem .5rem;display:block;margin:.25rem auto;font-size:18px;outline:none !important}.select__item:focus{color:#555;border-color:#555}.list{height:70%;overflow-y:scroll;text-align:left}.list::-webkit-scrollbar{display:none}.list__card{width:86%;margin:1rem auto;background-color:#eee;border:2px solid #d5d5d5;border-radius:10px;padding:0.5rem;box-shadow:0.25rem 0.25rem 0.6rem #13415a}.list__card__body{padding:.5rem;line-height:1.2}.list__card__body__title{font-size:20px;line-height:1.2;font-weight:700}.list__card button{width:100%;padding:0.75rem 0.5rem;border:0;border-radius:10px;font-size:16px;font-weight:700;color:#216e99;background-color:#eee;cursor:pointer;transition:.2s;outline:none !important}.list__card button:hover{background-color:#216e99;color:#eee}.green{color:green}.yellow{color:yellow}.orange{color:orange}.red{color:red}

.mainMap{width:100%;height:92%;transition:.2s}@media screen and (max-width: 992px){.mainMap{height:85%}}.mainMap.show{width:75%;margin-left:25%}@media screen and (max-width: 992px){.mainMap.show{width:100%;margin-left:0}}.legend{background:#fff;padding:8px;border-radius:5px;width:80%}.legend__lebel{display:flex;width:80%;line-height:30px;font-size:12px;font-weight:700}.legend i{display:block;width:30px;height:30px;margin-right:5px}

* {
  font-family: 'Noto Sans TC', sans-serif;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
  z-index: 20;
}

